.filter-drawer {
  position: fixed;
  top: 0;
  left: -30%;
  height: 100vh;
  width: 25%;
  background: #fff;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.2);
  padding: 20px;
  transition: all 0.3s ease-in-out;
  z-index: 2000;
}

.filter-drawer.open {
  left: 0;
}

.filter-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(255, 255, 255, 0.5);
  /* brighter overlay */
  backdrop-filter: blur(2px);
  z-index: 1500;
}

.close-btn {
  position: absolute;
  top: 6px;
  right: 11px;
  font-size: 29px;
  font-weight: 100;
  background: none;
  border: none;
  cursor: pointer;
}

.filterBoxSearchContainer {

  margin: 20px 0 34px 0;
  /* border: 1px solid red; */
}

.filterBoxSearchContainer>input {
  padding: 12px 22px 12px 6px;
  font-family: system-ui;
  font-size: 14px;
  opacity: 0.6;
  border-radius: 0;
  border: 1px solid #d5d5d5;
  height: 100%;
}

.filterBoxSearchContainer>button {
  padding: 12px 16px;
  font-family: 'Droid Sans';
  letter-spacing: 2px;
  text-transform: uppercase;
  background-color: #ffaa2c;
  border: 1px solid #ffaa2c;
}


.filterByPriceContainer {
  margin: 30px 0;
}

.filterByPriceContainer>h3 {
  font-family: monospace;
  font-weight: 400;
  font-size: 1.2rem;
  margin-bottom: 11px;
  color: #353535;
}
.filterByCategoryContainer>h3 {
  font-family: monospace;
  font-weight: 400;
  font-size: 1.2rem;
  margin-bottom: 11px;
  
  color: #353535;
}


.filterByPriceContainer > div{
  font-size: 12px;
    font-family: sans-serif;
    opacity: 0.6;
    margin-top: 4px;
}




.filterBox {
  width: 10vmax;
  /* position: absolute; */
  /* top: 10vmax; */
  /* left: 4vmax; */
}

.categoryBox {
  padding: 2px 0;
  margin-bottom: 1.1rem;
}

.category-link {
  list-style: none;
    color: rgb(41 41 41 / 61%);
    font: 400 1.1vmax "Roboto";
    margin: 0.3vmax 0.9vmax;
    cursor: pointer;
    transition: all 0.5s;
    letter-spacing: 1px;
}

.category-link:hover {
  color: #ffaa2c;
}

.filterBox>fieldset {
  border: 1px solid rgba(0, 0, 0, 0.329);
}

@media screen and (max-width: 600px) {
  .filterBox {
    width: 20vmax;
    position: static;
    margin: auto;
  }

  .categoryBox {
    margin-bottom: 15px;
  }

  .category-link {
    font: 400 1.8vmax "Roboto";
  }
}